@media (min-width: 300px) and (max-width: 599px){
    .wrapper { overflow-y: unset !important; }
}

.wrapper {
    grid-area: main;
    /*background: ghostwhite;*/
    overflow-y: scroll;
}

.wrapper > div {
    margin-bottom: 20px;
}

.wrapper > div:last-child {
    margin-bottom: 40px;
}